.connect {
  
}
.connect .container {
  padding-top: 5.5rem;
  padding-bottom: 8rem;
}
.connect__inner {
  border-radius: 1rem;
  padding: 7rem 0 7rem 0;
  background-color: #7E7E7E;
}
.left {
}
.form {
  padding: 5rem 6rem 3rem 6rem;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 1rem;
  max-width: 52rem;
  margin: 0 8rem 0 auto;
}
.form__title {
  color: #ffffff;
  font-family: $secondFont;
  font-weight: 700;
  font-size: 2.8rem;
  line-height: 3.8rem;
  text-align: center;
}
.form__data-field {
  display: flex;
  flex-direction: column;
}
form input, textarea {
  border-radius: 1rem;
  outline: none;
  border: none;
  padding: 1.3rem 0 1.3rem 3.2rem;
}
input::placeholder, 
textarea::placeholder {
  font-family: $secondFont;
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: rgba(51, 51, 51, 0.5);
}
.form__input-name {
  margin: 3rem 0 2rem 0;
}
.form__input-phone {
}
.form__textarea {
  margin: 2rem 0 3rem 0;
  height: 9rem;
  resize: vertical;
}
.form__button {
  width: 30rem;
  height: 5rem;
  margin: 0 auto;
  font-family: $secondFont;
  background-color: #333333;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.7rem;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
.form__descr {
  font-family: $secondFont;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-align: center;
  color: #333333;
}