.intro {
}
.intro .container {
  padding-bottom: 4.5rem;
}
.intro__inner {
  padding: 9.2rem 0 15.4rem 0;
}
.intro__location {
  display: flex;
  align-items: flex-end;
  margin-bottom: 3.6rem;
}
.intro__location-title {
  font-family: $secondFont;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.9rem;
  margin-right: 2rem;
}
.intro__location-list {
  display: flex;
  gap: 2rem;
  font-family: $secondFont;
  font-weight: 700;
}
.intro__location-item {
}
.intro__title {
  font-family: $thirdFont;
  font-weight: 900;
  font-size: 4rem;
  line-height: 4.8rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.intro__subtitle {
  font-family: $thirdFont;
  font-weight: 500;
  font-size: 2.8rem;
  line-height: 3.4rem;
  text-transform: uppercase;
  margin-bottom: 3.2rem;
}
.intro__text {
  margin-bottom: 4.8rem;
}
.intro__button {
  height: 6rem;
}