}
.terms__title {
  margin-bottom: 3.9rem;
}
.terms__content {
  display: flex;
  justify-content: space-between;
}
.terms__item {
  outline: 1px solid cyan;
}
.terms__caption {
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 2.8rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.terms__item-list {
}

.terms__list-item {
  margin-left: 2rem;
  list-style-type: square;
  margin-bottom: 2rem;
}
.terms__list-item:last-child {
  margin-bottom: 0;
}