.feedback__content { //product-wrapper
  display: grid;
  grid-template-columns: repeat(3, minmax(30rem, 40rem));
  gap: 2rem 4rem;
}
.feedback__item { //product
  padding: 3rem 2rem 3.2rem 3rem;
  display: flex;
  outline: 1px solid orange;
  box-shadow: 0px 0.4rem 1rem rgba(0, 0, 0, 0.25), 0px 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
}
.feedback__item:nth-child(3n) {
  margin-right: 0;
}
.feedback__image {
  flex: 1 1 9.4rem;
  outline: 1px solid blue;
  margin-right: 2.6rem;
}
.feedback__description {
  flex: 1 1 23rem;
  outline: 1px solid blueviolet;
  display: flex;
  flex-direction: column;
  justify-content: space-between
}
.feedback__text {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.8rem;
  margin-bottom: 2rem;
}
