.advantages {
}
.advanrages__inner {
}
.advantages__title {
  margin-bottom: 5.96rem;
}
.advantages__content {
  display: flex;
  gap: 7.5rem;
}
.advantages__item {
  flex: 0 1 25rem;
  outline: 1px solid magenta;
}
.advantages__number {
  position: relative;
  font-family: $thirdFont;
  font-size: 9.2rem;
  font-weight: 700;
  line-height: 5.6rem;
  color: rgba(1, 68, 147, 0.2);
  display: inline-block;
  margin-bottom: 1.5rem;
}
.advantages__number::before {
  content: '';
  position: absolute;
  z-index: 4;
  width: 100%;
  height: 1.8rem;
  background-color: #ffffff;
  bottom: -1rem;
  left: 0;
}
.advantages__number::after {
  content: '';
  position: absolute;
  z-index: 5;
  width: 100%;
  height: 0.2rem;
  background-color: #333333;
  bottom: -0.2rem;
  left: 0;
}
.advantages__caption {
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.5rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.advantages__text {
  font-weight: 500;
  line-height: 2rem;
}