.autopark {
}
.autopark__inner {
}
.autopark__title {
  margin-bottom: 5rem;
}
.autopark__content {
  display: flex;
  gap: 2rem;
  margin-bottom: 5rem;
}
.autopark__item {
  flex: 1 1 25%;
  outline: 1px solid green;
}
.autopark__image {
  margin-bottom: 2rem;
}
.autopark__caption {
  font-weight: 900;
  font-size: 2.2rem;
  line-height: 2.8rem;
  text-transform: uppercase;
}
.autopark__class {
  font-weight: 500;
  line-height: 2rem;
  color: #ADADAD;
}
.autopark__price {
  margin: 1rem 0 2rem 0;
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.5rem;
}
.autopark__button {
  width: 20rem;
  height: 5rem;
}
.autopark__link {
  display: block;
  text-align: center;
  
}
.autopark__link a {
  position: relative;
  color: #ADADAD;
}
.autopark__link a::after {
  content: '';
  position: absolute;
  width: 19.6rem;
  height: 0.1rem;
  background-color: #ADADAD;
  bottom: -0.6rem;
  left: -0.3rem;
}